:root {
  --main-green: #88d8b0;
  /*--main-background: #ffeead; */
  --main-background: #fff8da;
  --main-dark-green: rgb(91,144,117);
  --main-font-dark: rgb(17,3,0);
}
body {
  color: var(--main-font-dark);
  font-family: 'Lobster', cursive;
}
h1 {
  font-size: 5rem;
  margin-top: 0;
}
header {
  padding-bottom: 1rem;
  text-align: center;
  border-bottom: 0.2rem dotted var(--main-green);
  border-radius: 1rem;
  border-top: 1rem groove var(--main-dark-green);
  border-top-left-radius: 2rem;
  border-top-right-radius: 2rem;
  z-index: 0;
  word-wrap: break-word;
}
header h2 {
  font-size: 2rem;
}
ul {
  list-style-type: none;
}
.content {
  background-color: white;
  padding: 3rem;
  font-family: 'Questrial', sans-serif;
  font-size: 1.2rem;
  border-left: 0.2rem dotted var(--main-green);
  border-right: 0.2rem dotted var(--main-green);
  border-top: 0.1rem groove var(--main-dark-green);
  border-bottom: 0.12rem groove var(--main-dark-green);
  max-width: 95%;
  margin-left: auto;
  margin-right: auto;
  align-self: center;
  z-index: 2;
}
a {
  text-decoration: none;
  color: inherit;
}
.notification {
  display: none;
 z-index: 2;
 position:absolute;
 top: 10rem;
 border: 1rem solid var(--main-green);
}
.navlink {
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: space-around;
  align-content: space-around;
  flex-wrap: wrap;
}
.topnav .login {

}
.topnav .link {
  font-size: 1.8rem;
  display: flex;
  flex-direction: row;
  padding: 1rem;
  margin: .2rem;
  border: 0.2rem solid var(--main-dark-green);
  width: 5rem;
  min-height: 6rem;
  max-width: 5rem;
  transition: 150ms linear;
  justify-content: center;
}
.topnav .link:hover {
  background-color: var(--main-dark-green);
  box-shadow: 0 0 1rem var(--main-dark-green);
}
footer {
  display: flex;
  align-content: center;
  justify-content: center;
  min-height: 5rem;
  border-radius: 1rem;
  border-top: 0.2rem dotted var(--main-green);
  border-bottom: 1rem groove var(--main-dark-green);
  box-shadow: 0rem 1rem 1.5rem var(--main-green);
  border-bottom-left-radius: 2rem;
  border-bottom-right-radius: 2rem;
  font-size: 1.2rem;
}
.ticket-text {
  min-width: 100%;
  min-height: 10rem;
  font-family: inherit;
  flex-wrap: wrap;
}
.ticket-form {
  display: flex;
  flex-direction: column;
  line-height: 1.5rem;
}
.tickets {
  display: flex;
  flex-direction: column;
  align-content: space-between;
  height: 100%;
}
.ticket-input-title {
  font-size: 1rem;
  font-family: inherit;
  min-width: 100%;
}
.comment-input {
  font-size: 1rem;
  font-family: inherit;
  min-width: 100%;
}
.ticket {
  border-top-left-radius: 2rem;
  border-bottom-left-radius: 2rem;
  border-top: 1rem groove var(--main-green);
  padding: 2rem;
  padding-left: 0.6rem;
  padding-top: 0.5rem;
  border-left: 0.2rem solid var(--main-green);
  border-bottom: 0.2rem solid var(--main-green);
  transition: 200ms linear;
  margin-top: 2rem;
}
.ticket-title {
  font-size: 1.5rem;
}
.ticket-header {
  border-bottom: 0.2rem solid var(--main-dark-green);
}
.ticket:hover {
  box-shadow: 0 0rem 2rem var(--main-green);
}
button {
  font-size: inherit;
  font-family: inherit;
}
.login-wrapper {
  display: flex;
  flex-direction: row;
  line-height: 1.5rem;
}
.register-form-wrapper {
  display: flex;
  flex-direction: row;
  line-height: 1.5rem;
}
.comment-form {
  padding-top: 1rem;
  line-height: 1.2rem;
}
.personal-info-wrapper {
  max-width: 50%;
  display: flex;
  flex-direction: row;
  line-height: 1.5rem;
  justify-content: space-evenly;
}
.personal-info-wrapper .labels {
  border-left: 1.2rem dotted var(--main-green);
  padding-left: 1rem;
}
.manage-nav {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  border-bottom: 0.2rem solid var(--main-green);
  padding-bottom: 0.5rem;
  
}
.manage-nav .link {
  padding: 0.5rem;
  transition: font-size 150ms linear;
}
.manage-nav .link:hover {
  background-color: var(--main-green);
  font-size: 1.6rem;
}
.user {
  border-top: 1rem groove var(--main-green);
  padding: 2rem;
  padding-top: 1rem;
  border-left: 0.2rem solid var(--main-green);
  border-bottom: 0.2rem solid var(--main-green);
  transition: 200ms linear;
  margin-top: 2rem;
  border-top-left-radius: 2rem;
  border-bottom-left-radius: 2rem;
}
.user:hover {
  box-shadow: 0 0rem 2rem var(--main-green);
}
.comment-author {
  font-style: italic;
  background-color: var(--main-green);
  background: linear-gradient(90deg, rgba(136,216,176,1) 0%, rgba(136,216,176,1) 2%, rgba(255,248,218,1) 100%);
  

}
.comment-text {
  text-align: left;
  word-wrap: break-word;
  padding: 0.5rem;
  padding-bottom: 1.2rem;
}
.comment-id {
  text-align: right;
  background-color: var(--main-green);
  background: linear-gradient(270deg, rgba(136,216,176,1) 0%, rgba(136,216,176,1) 0.1rem, rgba(255,248,218,1) 100%);
}
.comment-wrapper {
  display: flex;
  flex-direction: column;
  word-wrap: break-word;
  flex-wrap: wrap;
  border-bottom: .2rem dotted var(--main-green);
}
.comments.wrapper {
 border-top: 1rem groove var(--main-green);
 padding-top: 0.3rem;
}
button {
  background-color: var(--main-green);
}